import { Axios } from "../helpers/axios.config";

export const listarInventarios = () => {
    return Axios.get('inventario');
}
export const getInventarios = (inventarioId) => {
    return Axios.get(`inventario/${inventarioId}`);
}

export const crearInventarios = (data) => {
    return Axios.post('inventario', data);
}

export const editarInventarios = (inventarioId, data) => {
    return Axios.put(`inventario/${inventarioId}`, data);
}

export const eliminarInventario = (inventarioId) => {
    return Axios.delete(`inventario/${inventarioId}`);
}