import { Axios } from "../helpers/axios.config";

export const listarTipos = () => {
    return Axios.get('tipoequipo');
}

export const crearTipos = (data) => {
    return Axios.post('tipoequipo', data);
}

export const editarTipos = (tipoequipoId, data) => {
    return Axios.put(`tipoequipo/${tipoequipoId}`, data);
}

export const eliminarTipos = (tipoequipoId) => {
    return Axios.delete(`tipoequipo/${tipoequipoId}`);
}

export const getTipo = (tipoequipoId) => {
    return Axios.get(`tipoequipo/${tipoequipoId}`);
}