import React from "react";
import { NavLink } from "react-router-dom";

export const Menu = () => {
  return (
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
          <div className="container-fluid">
            <NavLink className="navbar-brand active" exact to="/inventario">Inventarios</NavLink>
            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarTogglerDemo02" aria-controls="navbarTogglerDemo02" aria-expanded="false" aria-label="Toggle navigation">
              <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarTogglerDemo02">
              <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                <li className="nav-item">
                  <NavLink className="nav-link" exact to="/usuario">Usuarios</NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" exact to="/marca">Marcas</NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" exact to="/tipoequipo">Tipos</NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" exact to="/estadoequipo">Estados</NavLink>
                </li>
              </ul>
            </div>
          </div>
        </nav>
  );
}
