import React from "react";
import { Link } from 'react-router-dom';

export const InventarioCard = (props) => {

  const {inventario} = props;

  return (
      <div className="col">
        <div className="card">
          <img src={inventario.foto} className="card-img-top" alt="Foto del Activo" />
          <div className="card-body">
            <h6 className="card-title">CARACTERISTICAS</h6>
            <hr />
            <p className="card-text">{inventario.serial}</p>
            <p className="card-text">{inventario.modelo}</p>
            <p className="card-text">{inventario.marca.nombre}</p>
            <p className="card-text">{inventario.descripcion}</p>
            <p className="card-text">{inventario.color}</p>
            <p className="card-text">{inventario.precio}</p>
            <Link to={`inventario/edit/${inventario._id}`}>ver mas...</Link>
          </div>
        </div>
      </div>
  );
};
