import React from 'react'
import { InventarioNuevo } from '../inventarios/InventarioNuevo';

export const CrearInventario = ({ handleModalInventario}) => {

  const reload = (e)=>{
    e.preventDefault();
    handleModalInventario();
    window.location.reload();
  }

  return<>
    <div className='nuevoInventario'>   
        <div className='Container-fluid'>
          <div className='row'>
            <div className='col'>
              <div className='nuevoInventario-header'>
                <h2>Activo Nuevo</h2>              
                  <button className='btn btn-secondary cerrarInventario' onClick={reload}>X</button>
              </div>
            </div>
          </div>
          <hr/>
          <InventarioNuevo handleModalInventario = { handleModalInventario }/>  
          <hr/>
        </div>
    </div>  
 </>
}
