import { useState, useEffect } from "react";
import BrandTable from "./TablaEstado";
import { listarEstados } from '../../services/estados.service'
import { EstadoNuevo } from '../estados/CrearEstado';

import Swal from "sweetalert2";

export const ListarEstados = () => {
  const [estados, setEstados] = useState([]);

      const mostrarEstados = async () => {
        try {
          const { data } = await listarEstados();
          setEstados(data);
          Swal.fire({
            title:"Estados de equipo",
            allowOutsideClick: false,
            text: 'Cargando....',
            timer: 500
          });
          Swal.showLoading();
          
        } catch (error) {
          let mensaje;
          if (error && error.response && error.response.data){
            mensaje = error.response.data
          }else
          {
            mensaje = "Error de servidor, verifique el backend"
          }
          Swal.fire("Error", mensaje, "error");
        }
      };
    
    useEffect(() => {
        mostrarEstados();
    }, []);
    
    
      const DataTable = () => {

          return estados.map((res, i) => {
        
              return <BrandTable obj={res} key={i}/>;
          });
          
      };

      return (
        
          <div>
             < EstadoNuevo />
             <div className="fila-crear">
             <hr/>
              </div>          
              <div>
                  <table className="table table-sm border-primary table-striped">
                      <thead className="table-group-divider" >
                          <tr className="table-warning">
                              <th>#</th>
                              <th >Nombre</th>
                              <th>Estado</th>
                              <th>Fecha de creacin</th>
                              <th>Fecha de Modificacin</th>
                              <th>Acciones</th>
                          </tr>
                      </thead>
                      <tbody className="table-group-divider">{DataTable()}</tbody>
                  </table>
              </div>
       
          </div>
      );
  };

