import React, { useState } from "react";
import { Link, Redirect } from "react-router-dom";
import { eliminarEstado} from "../../services/estados.service";
import Swal from "sweetalert2";
import { BsFillTrashFill, BsFillPenFill } from "react-icons/bs";

const BrandTable = (props) => {
  const { _id, nombre, estado, fechaCreacion, fechaActualizacion } = props.obj;

  const borrarEstado = async () => {
    Swal.fire({
      title: "Desea eliminar el estado de equipo?",
      showDenyButton: true,
      showCancelButton: true,
      confirmButtonText: "Guardar",
      denyButtonText: "No guardar",
      cancelButtonText: "regresar",
    }).then(async (result) => {
      let data = "";
      try {
        if (result.isConfirmed) {
          data = await eliminarEstado(_id);
          Swal.fire({
            title: 'Estado de equipo eliminado correctamente!',
            text: 'Eliminado',        
          });
          setTimeout(() => { 
            window.location.reload();           
      }, 5000);
        } else if (result.isDenied) {
          Swal.fire("Los cambios no fueron guardados", "", "info");
        }
      } catch (error) {
        let mensaje;
        if (error && error.response && error.response.data) {
          mensaje = error.response.data;
        } else {
          mensaje = "Error de servidor, verifique el backend";
        }
        Swal.fire("Error", mensaje, "error");
      } finally {
        setTimeout(() => {
            console.log(data);
      }, 5000);
      }
    });
  };

  const [modalestado, setModalEstado] = useState(false);

  const handleModalEstado = () => {
    setModalEstado(!modalestado);
  };

  return (
    <>
      <tr>
        <th scope="row">{`${Array.prototype.length++}`}</th>
        <td>{nombre}</td>
        <td>{estado}</td>
        <td>{fechaCreacion}</td>
        <td>{fechaActualizacion}</td>

        <td className="botones">
          {modalestado ? (
            <Redirect to ="/estadoequipo" />
          ) : (
            <Link className="edit-link" to={"/estadoequipo/edit/" + _id}>
              <button className="btn btn-secondary" onClick={handleModalEstado}>
                <BsFillPenFill />
              </button>
            </Link>
          )}
          <Link className="borrar-link" to={"/estadoequipo/borrar/" + _id}>
            <button className="btn btn-warning" onClick={borrarEstado}>
              <BsFillTrashFill />
            </button>
          </Link>
        </td>
      </tr>
    </>
  );
};

export default BrandTable;
