import React, { useState, useEffect } from "react";
import { crearTipos } from "../../services/tipos.service";
import Swal from "sweetalert2";
import { Link } from "react-router-dom";

export const TipoNuevo = () => {

  const [valoresForm, setValoresForm] = useState({});
  const {
    nombre = "",
    estado = ""
  } = valoresForm;

  useEffect(() => {
 limpiar();
  }, [])
  
  const limpiar = () => {
    setValoresForm({
      nombre: "",
      estado: "",
    });

  };

  const handleOnChange = ({ target }) => {
    const { name, value } = target;
    setValoresForm({ ...valoresForm, [name]: value });
  };

  const handleOnSubmit = (e) => {
    let timerInterval
    e.preventDefault();
    const tipo = {
      nombre,
      estado,
    };
    Swal.fire({
      title: "Desea guardar el tipo de equipo?",
      showDenyButton: true,
      showCancelButton: true,
      confirmButtonText: "Guardar",
      denyButtonText: "No guardar",
      cancelButtonText: "regresar",
      html:
          'No se guardaran los cambios en: <strong></strong> seconds.<br/><br/>',
      timer: 10000,
      didOpen: () => {
      
        timerInterval = setInterval(() => {
          Swal.getHtmlContainer().querySelector('strong')
            .textContent = (Swal.getTimerLeft() / 1000)
              .toFixed(0)
        }, 1000)
      },
      willClose: () => {
        clearInterval(timerInterval)
      }
    }).then(async (result) => {
      let data = '';
      try {
        if (result.isConfirmed) {
          data = await crearTipos(tipo);
          Swal.fire(
            "Tipo de equipo guardado correctamente!",
            "",
            "success"
          );     
        } else if (result.isDenied) {
          Swal.fire("Los cambios no fueron guardados", "", "info");
        }
      } catch (error) {
        let mensaje;
        if (error && error.response && error.response.data){
          mensaje = error.response.data
        }else
        {
          mensaje = "Error de servidor, verifique el backend"
        }
        console.log(mensaje);
        Swal.fire("Error", mensaje, "error");    
      } finally {
        setTimeout(() => {
          console.log(data);
          limpiar();
          window.location.reload();
        }, 5000);
      }
    });
  };

  return (
    <form className="formTipos" onSubmit={handleOnSubmit}>
      <div className="row TipoNuevo row-cols-2">
        <div className="col">
          <div className="mb-3">
            <label className="form-label">Nombre</label>
            <input
              type="text"
              name="nombre"
              value={nombre}
              onChange={(e) => handleOnChange(e)}
              minLength={4}
              required
              className="form-control"
              placeholder="Nombre del tipo de equipo"
            />
          </div>
        </div>
        <div className="col">
          <div className="mb-3">
            <label htmlFor="estado" className="form-label">
              Estado
            </label>
            <select
              className="form-select"
              onChange={(e) => handleOnChange(e)}
              name="estado"
              value={estado}
              required
            >
              <option value="">--SELECCIONE--</option>
                <option value="Activo">Activo</option>
                <option value="Inactivo">Inactivo</option>
            </select>
          </div>
        </div>
      </div>
      <div className="botones">
        <button className="btn btn-primary">Guardar</button>
        <Link to={'/inventario'}><button className="btn btn-primary" >Inventario</button>
        </Link>
      </div>
    </form>
  );
};