import React, { useState, useEffect } from "react";
import { useParams, Redirect} from "react-router-dom";
import { getMarca, editarMarcas } from "../../services/marca.service";

import Swal from "sweetalert2";

export const ActualizarMarca = () => {
  const { marcaId } = useParams();

  const [marca, setMarca] = useState({});

  const [valoresForm, setValoresForm] = useState({});
  const { nombre = "", estado = "" } = valoresForm;

  useEffect(() => {
    const mostrarMarcas = async () => {
      try {
        const { data } = await getMarca(marcaId);
        setMarca(data);
        Swal.fire({
          title: "Marcas",
          allowOutsideClick: false,
          text: "Cargando....",
          timer: 500,
        });
        Swal.showLoading();
      } catch (error) {
        let mensaje;
        if (error && error.response && error.response.data) {
          mensaje = error.response.data;
        } else {
          mensaje = "Error de servidor, verifique el backend";
        }
        Swal.fire("Error", mensaje, "error");
      }
    };
    mostrarMarcas();
  }, [marcaId]);

  useEffect(() => {
    setValoresForm({
      nombre: marca.nombre,
      estado: marca.estado,
    });
  }, [marca]);

  const handleOnChange = ({ target }) => {
    const { name, value } = target;
    setValoresForm({ ...valoresForm, [name]: value });
  };

  const handleOnSubmit = () => {
    let timerInterval;
    const marca = { nombre, estado };
    Swal.fire({
      title: "Desea guardar la marca?",
      showDenyButton: true,
      showCancelButton: true,
      confirmButtonText: "Guardar",
      denyButtonText: "No guardar",
      cancelButtonText: "regresar",

      html:
          'No se guardaran los cambios en: <strong></strong> seconds.<br/><br/>',
      timer: 10000,
      didOpen: () => {
      
        timerInterval = setInterval(() => {
          Swal.getHtmlContainer().querySelector('strong')
            .textContent = (Swal.getTimerLeft() / 1000)
              .toFixed(0)
        }, 1000)
      },
      willClose: () => {
        clearInterval(timerInterval)
      }
    })
      .then(async (result) => {
        let data = "";
        try {
          if (result.isConfirmed) {
            data = await editarMarcas(marcaId, marca);
            Swal.fire("MARCA actualizada correctamente!", "", "success");
          } else if (result.isDenied) {
            Swal.fire("Los cambios no fueron guardados", "", "info");
            data = "No se realizo ningun cambio";
          }
        } catch (error) {
          let mensaje;
          if (error && error.response && error.response.data) {
            mensaje = error.response.data;
          } else {
            mensaje = "Error de servidor, verifique el backend";
          }
          Swal.fire("Error", mensaje, "error");
        } finally {
          setTimeout(() => {
              console.log(data);
        }, 5000);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const [modalactualizar, setModalActualizar]= useState(false);

  const handleModalActualizar = () =>{
    
    try {
      if (!modalactualizar){
        
        handleOnSubmit();
      }
    } catch (error) {
       setModalActualizar(!modalactualizar);
    } finally {
      setTimeout(() => {
          setModalActualizar(!modalactualizar);
      }, 5000);
    }
  }

  return (
    <div className="container-fluid mt-3 mb-2">
      <h5>MARCAS</h5>

      <div className="row">
        <div className="col-md-12">
          <form className="formInventarios">
            <div className="row InventarioNuevo row-cols-2">
              <div className="col">
                <div className="mb-3">
                  <label className="form-label">Nombre</label>
                  <input
                    type="text"
                    name="nombre"
                    value={nombre}
                    onChange={(e) => handleOnChange(e)}
                    minLength={4}
                    required
                    className="form-control"
                    placeholder="Nombre del Activo"
                  />
                </div>
              </div>
              <div className="col">
                <div className="mb-3">
                  <label htmlFor="estado" className="form-label">
                    Estado
                  </label>
                  <select
                    className="form-select"
                    onChange={(e) => handleOnChange(e)}
                    name="estado"
                    value={estado}
                    required
                  >
                    <option value="">--SELECCIONE--</option>
                    <option value="Activo">Activo</option>
                    <option value="Inactivo">Inactivo</option>
                  </select>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="botones">
              {
      modalactualizar ?
        < Redirect to ="/marca" />
      :
        <button type="button" className="btn btn-primary" onClick={handleModalActualizar}>Actualizar </button>
        
    } 
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};
