import React, { useState, useEffect } from "react";
import { useParams, Redirect } from "react-router-dom";
import { getUsuario, editarUsuarios } from "../../services/usuario.service";

import Swal from "sweetalert2";

export const ActualizarUsuario = () => {
  const { usuarioId } = useParams();

  const [usuario, setUsuario] = useState({});

  const [valoresForm, setValoresForm] = useState({});
  const { nombre = "", email = "", estado = "" } = valoresForm;

  useEffect(() => {
    const mostrarUsuarios = async () => {
      try {
        const { data } = await getUsuario(usuarioId);
        setUsuario(data);
        Swal.fire({
          title: "Usuarios",
          allowOutsideClick: false,
          text: "Cargando....",
          timer: 500,
        });
        Swal.showLoading();
      } catch (error) {
        let mensaje;
        if (error && error.response && error.response.data) {
          mensaje = error.response.data;
        } else {
          mensaje = "Error de servidor, verifique el backend";
        }
        Swal.fire("Error", mensaje, "error");
      }
    };
    mostrarUsuarios();
  }, [usuarioId]);

  useEffect(() => {
    setValoresForm({
      nombre: usuario.nombre,
      email: usuario.email,
      estado: usuario.estado,
    });
  }, [usuario]);

  const handleOnChange = ({ target }) => {
    const { name, value } = target;
    setValoresForm({ ...valoresForm, [name]: value });
  };

  const handleOnSubmit = () => {
    let timerInterval;
    const usuario = { nombre, email, estado };
    Swal.fire({
      title: "Desea guardar el usuario?",
      showDenyButton: true,
      showCancelButton: true,
      confirmButtonText: "Guardar",
      denyButtonText: "No guardar",
      cancelButtonText: "regresar",

      html:
        'No se guardaran los cambios en: <strong></strong> seconds.<br/><br/>',
      timer: 10000,
      didOpen: () => {

        timerInterval = setInterval(() => {
          Swal.getHtmlContainer().querySelector('strong')
            .textContent = (Swal.getTimerLeft() / 1000)
              .toFixed(0)
        }, 1000)
      },
      willClose: () => {
        clearInterval(timerInterval)
      }
    })
      .then(async (result) => {
        let data = "";
        try {
          if (result.isConfirmed) {
            data = await editarUsuarios(usuarioId, usuario);
            Swal.fire("Usuario actualizado correctamente!", "", "success");
          } else if (result.isDenied) {
            Swal.fire("Los cambios no fueron guardados", "", "info");
            data = "No se realizo ningun cambio";
          }
        } catch (error) {
          let mensaje;
          if (error && error.response && error.response.data) {
            mensaje = error.response.data;
          } else {
            mensaje = "Error de servidor, verifique el backend";
          }
          Swal.fire("Error", mensaje, "error");
        } finally {
          setTimeout(() => {
            console.log(data);
          }, 5000);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const [modalactualizar, setModalActualizar] = useState(false);

  const handleModalActualizar = () => {

    try {
      if (!modalactualizar) {

        handleOnSubmit();
      }
    } catch (error) {
      setModalActualizar(!modalactualizar);
    } finally {
      setTimeout(() => {
        setModalActualizar(!modalactualizar);
      }, 5000);
    }
  }

  return (
    <div className="container-fluid mt-3 mb-2">
      <h5>USUARIOS</h5>

      <div className="row">
        <div className="col-md-12">
          <form className="formInventarios">
            <div className="row InventarioNuevo row-cols-3  ">
              <div className="col">
                <div className="mb-3">
                  <label className="form-label">Nombre</label>
                  <input
                    type="text"
                    name="nombre"
                    value={nombre}
                    onChange={(e) => handleOnChange(e)}
                    minLength={4}
                    required
                    className="form-control"
                    placeholder="Nombre del Activo"
                  />
                </div>
              </div>
              <div className="col">
                <div className="mb-3">
                  <label className="form-label">Email</label>
                  <input
                    type="text"
                    name="email"
                    value={email}
                    onChange={(e) => handleOnChange(e)}
                    minLength={6}
                    required
                    className="form-control"
                    placeholder="Correo electronico"
                  />
                </div>
              </div>
              <div className="col">
                <div className="mb-3">
                  <label htmlFor="estado" className="form-label">
                    Estado
                  </label>
                  <select
                    className="form-select"
                    onChange={(e) => handleOnChange(e)}
                    name="estado"
                    value={estado}
                    required
                  >
                    <option value="">--SELECCIONE--</option>
                    <option value="Activo">Activo</option>
                    <option value="Inactivo">Inactivo</option>
                  </select>
                </div>
              </div>
            </div>
            <div className="row">
              <div className="botones">
                {
                  modalactualizar ?
                    < Redirect to="/usuario" />
                    :
                    <button type="button" className="btn btn-primary" onClick={handleModalActualizar}>Actualizar </button>

                }
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};
