import React from "react";
import { BrowserRouter as Router, Switch, Route, Redirect } from "react-router-dom";
import { Menu } from "./Menu.component";

import { Usuario } from "../../views/Usuario";
import { Marca } from "../../views/Marca";
import { Inventario } from "../../views/Inventario";
import { TipoEquipo } from "../../views/TipoEquipo";
import { EstadoEquipo } from "../../views/EstadoEquipo";

import { ActualizarInventario } from "../inventarios/ActualizarInventario";
import { ActualizarMarca } from "../marcas/ActualizarMarca";
import { ActualizarEstado } from "../estados/ActualizarEstado";
import { ActualizarTipo } from "../tipos/ActualizarTipo";
import { ActualizarUsuario } from "../usuarios/ActualizarUsuario";
import { ListarMarcas } from "../marcas/ListarMarca";
import { ListarEstados } from "../estados/ListarEstado";
import { ListarTipos } from "../tipos/ListarTipo";
import { ListarUsuarios } from "../usuarios/ListarUsuario";

export const Rutas = () => {

    return(
        <Router>
            < Menu />
            <Switch>
                <Route exact path="/" component={Inventario} />
                <Route exact path="/inventario" component={Inventario} />
                <Route exact path="/inventario/edit/:inventarioId" component={ActualizarInventario} />
                <Route exact path="/usuario" component={Usuario} />
                <Route exact path="/usuario/edit/:usuarioId" component={ActualizarUsuario} />
                <Route exact path="/usuario/borrar/:usuarioId" component={ListarUsuarios} />
                <Route exact path="/tipoequipo" component={TipoEquipo} />
                <Route exact path="/tipoequipo/edit/:tipoequipoId" component={ActualizarTipo} />
                <Route exact path="/tipoequipo/borrar/:tipoequipoId" component={ListarTipos} />
                <Route exact path="/estadoequipo" component={EstadoEquipo} />
                <Route exact path="/estadoequipo/edit/:estadoId" component={ActualizarEstado} />
                <Route exact path="/estadoequipo/borrar/:estadoId" component={ListarEstados} />
                <Route exact path="/marca" component={Marca} />
                <Route exact path="/marca/edit/:marcaId" component={ActualizarMarca} />
                <Route exact path="/marca/borrar/:marcaId" component={ListarMarcas} />
                <Redirect to="/" />
            </Switch>
    </Router>
    );
}