import { useState, useEffect } from "react";
import { InventarioCard } from "./InventarioCard";

import { listarInventarios } from "../../services/inventario.service";
import { CrearInventario } from './CrearInventario';
import { BsPlusLg }  from 'react-icons/bs';


import Swal from "sweetalert2";

export const ListarInventarios = () => {
  const [inventarios, setInventarios] = useState([]);
  const [modalinventario, setModalInventario]= useState(false);

   const mostrarInventario = async () => {
     try {
        const { data } = await listarInventarios();
        setInventarios(data);
        Swal.fire({
          title:"Inventarios",
          allowOutsideClick: false,
          text: 'Cargando....',
          timer: 500
        });
        Swal.showLoading();
      } catch (error) {
        let mensaje;
        if (error && error.response && error.response.data){
          mensaje = error.response.data
        }else
        {
          mensaje = "Error de servidor, verifique el backend"
        }
        Swal.fire("Error", mensaje, "error");
      }
  };
  
  useEffect(() => {
    mostrarInventario();
  }, []);

  const handleModalInventario = () =>{
    setModalInventario(!modalinventario)
  }

  return (
    <div className="container">
      <div className="row mt-2 mb-2 row-cols-1 row-cols-md-4 g-4">
        {
          inventarios.map((inventario) => {
            return < InventarioCard key={inventario._id} inventario = { inventario }/>            
          })
        }
      </div>
      {
        modalinventario ?
          < CrearInventario 
                handleModalInventario = { handleModalInventario }     
          />
        :
          <button type="button" className="botones btn btn-primary invNuevo" onClick={handleModalInventario}><BsPlusLg /></button>
      }    
    </div>
  );
};
