import { Axios } from "../helpers/axios.config";

export const listarUsuarios = () => {
    return Axios.get('usuario');
}

export const crearUsuarios = (data) => {
    return Axios.post('usuario', data);
}

export const editarUsuarios = (usuarioId, data) => {
    return Axios.put(`usuario/${usuarioId}`, data);
}

export const eliminarUsuarios = (usuarioId) => {
    return Axios.delete(`usuario/${usuarioId}`);
}

export const getUsuario = (usuarioId) => {
    return Axios.get(`usuario/${usuarioId}`);
}