import React, { useState, useEffect } from "react";
import { listarUsuarios } from "../../services/usuario.service";
import { listarEstados } from "../../services/estados.service";
import { listarMarcas } from "../../services/marca.service";
import { listarTipos } from "../../services/tipos.service";
import { crearInventarios } from "../../services/inventario.service";
import moment from "moment";
import Swal from "sweetalert2";

export const InventarioNuevo = ({
  handleModalInventario
}) => {
  const [usuarios, setUsuarios] = useState([]);
  const [marcas, setMarcas] = useState([]);
  const [tipos, setTipos] = useState([]);
  const [estados, setEstados] = useState([]);

  const fecha = new Date(),
    fechaFin = new Date();

  function sumarDias(fecha, dias) {
    fecha.setDate(fecha.getDate() + dias);
    return fecha;
  }

  const fechaInicio = sumarDias(fecha, -15);

  const [valoresForm, setValoresForm] = useState({});
  const {
    serial = "",
    modelo = "",
    descripcion = "",
    color = "",
    foto = "",
    fechaCompra = moment(new Date()).utc().format("YYYY-MM-DD"),
    precio = "",
    usuario,
    marca,
    tipoEquipo,
    estadoEquipo,
  } = valoresForm;

  const mostrarUsuarios = async () => {
    try {
      const { data } = await listarUsuarios();
      setUsuarios(data);
    } catch (error) {
      Swal.fire("Error", error.response.data, "error");
    }
  };

  useEffect(() => {
    mostrarUsuarios();
  }, []);

  const limpiar = () => {
    setValoresForm({
      serial: "",
      modelo: "",
      descripcion: "",
      color: "",
      foto: "",
      fechaCompra: moment(new Date()).utc().format("YYYY-MM-DD"),
      precio: "",
      usuario: "",
      marca: "",
      tipoEquipo: "",
      estadoEquipo: "",
    });
  };

  const mostrarEstados = async () => {
    try {
      const { data } = await listarEstados();
      setEstados(data);
    } catch (error) {
      Swal.fire("Error", error.response.data, "error");
    }
  };

  useEffect(() => {
    mostrarEstados();
  }, []);

  const mostrarMarcas = async () => {
    try {
      const { data } = await listarMarcas();
      setMarcas(data);
    } catch (error) {
      Swal.fire("Error", error.response.data, "error");
    }
  };

  useEffect(() => {
    mostrarMarcas();
  }, []);

  const mostrarTipos = async () => {
    try {
      const { data } = await listarTipos();
      setTipos(data);
    } catch (error) {
      Swal.fire("Error", error.response.data, "error");
    }
  };

  useEffect(() => {
    mostrarTipos();
  }, []);

  const handleOnChange = ({ target }) => {
    const { name, value } = target;
    setValoresForm({ ...valoresForm, [name]: value });
  };

  const reload = (e) => {
    e.preventDefault();
    handleModalInventario();
    window.location.reload();
  };

  const handleOnSubmit = (e) => {
    e.preventDefault();
    const inventario = {
      serial,
      modelo,
      descripcion,
      color,
      foto,
      fechaCompra,
      precio,
      usuario: {
        _id: usuario,
      },
      marca: {
        _id: marca,
      },
      tipoEquipo: {
        _id: tipoEquipo,
      },
      estadoEquipo: {
        _id: estadoEquipo,
      },
    };
    Swal.fire({
      title: "Desea guardar el activo?",
      showDenyButton: true,
      showCancelButton: true,
      confirmButtonText: "Guardar",
      denyButtonText: "No guardar",
      cancelButtonText: "regresar",
    }).then(async (result) => {
      let data='';
      try {
        if (result.isConfirmed) {
          data = await crearInventarios(inventario);
          Swal.fire(
            "Activo guardado correctamente!",
            "",
            "success"
          );
          
        } else if (result.isDenied) {
          Swal.fire("Los cambios no fueron guardados", "", "info");
          data = 'No se realizo ningun cambio'
        }
      } catch (error) {
        let mensaje;
        if (error && error.response && error.response.data){
          mensaje = error.response.data
        }else
        {
          mensaje = "Error de servidor, verifique el backend"
        }
        console.log(mensaje);
        Swal.fire("Error", mensaje, "error");    
      }finally {
        limpiar();
        console.log(data);
      }
    });
  };

  return (
    <form onSubmit={handleOnSubmit}>
      <div className="row InventarioNuevo row-cols-4">
        <div className="col">
          <div className="mb-3">
            <label className="form-label">Serial</label>
            <input
              type="text"
              name="serial"
              value={serial}
              onChange={(e) => handleOnChange(e)}
              minLength={4}
              required
              className="form-control"
              placeholder="Serial del Activo"
            />
          </div>
        </div>
        <div className="col">
          <div className="mb-3">
            <label htmlFor="modelo" className="form-label">
              Modelo
            </label>
            <input
              type="text"
              className="form-control"
              id="modelo"
              name="modelo"
              value={modelo}
              onChange={(e) => handleOnChange(e)}
              placeholder="Modelo del Activo"
              required
              minLength={4}
            />
          </div>
        </div>
        <div className="col">
          <div className="mb-3">
            <label htmlFor="descripcion" className="form-label">
              Descripcin
            </label>
            <input
              type="text"
              className="form-control"
              id="descripcion"
              name="descripcion"
              value={descripcion}
              onChange={(e) => handleOnChange(e)}
              placeholder="Descripcin del Activo"
              required
              minLength={4}
            />
          </div>
        </div>
        <div className="col">
          <div className="mb-3">
            <label htmlFor="color" className="form-label">
              Color
            </label>
            <input
              type="text"
              className="form-control"
              id="color"
              name="color"
              value={color}
              onChange={(e) => handleOnChange(e)}
              placeholder="Color del Activo"
              required
              minLength={4}
            />
          </div>
        </div>
      </div>
      <div className="row InventarioNuevo row-cols-4">
        <div className="col">
          <div className="mb-3">
            <label htmlFor="foto" className="form-label">
              Foto
            </label>
            <input
              type="url"
              className="form-control"
              id="foto"
              name="foto"
              value={foto}
              onChange={(e) => handleOnChange(e)}
              placeholder="Foto del Activo"
              required
            />
          </div>
        </div>
        <div className="col">
          <div className="mb-3">
            <label htmlFor="fechaCompra" className="form-label">
              Fecha de Compra
            </label>
            <input
              type="Date"
              className="form-control"
              id="fechaCompra"
              name="fechaCompra"
              min={moment(fechaInicio).utc().format("YYYY-MM-DD")}
              max={moment(fechaFin).utc().format("YYYY-MM-DD")}
              onChange={(e) => handleOnChange(e)}
              value={moment(fechaCompra).utc().format("YYYY-MM-DD")}
              required
            />
          </div>
        </div>
        <div className="col">
          <div className="mb-3">
            <label htmlFor="precio" className="form-label">
              Precio
            </label>
            <input
              type="number"
              className="form-control"
              id="precio"
              name="precio"
              value={precio}
              min="0"
              max= "100000000"
              onChange={(e) => handleOnChange(e)}
              placeholder="Precio del Activo"
              required
            />
          </div>
        </div>
        <div className="col">
          <div className="mb-3">
            <label htmlFor="usuario" className="form-label">
              Usuario
            </label>
            <select
              className="form-select"
              onChange={(e) => handleOnChange(e)}
              name="usuario"
              value={usuario}
              required
            >
              <option value="">--SELECCIONE--</option>
              {usuarios.map(({ _id, nombre }) => {
                return (
                  <option key={_id} value={_id}>
                    {nombre}
                  </option>
                );
              })}
            </select>
          </div>
        </div>
      </div>
      <div className="row InventarioNuevo row-cols-3">
        <div className="col">
          <div className="mb-3">
            <label htmlFor="marca" className="form-label">
              Marca
            </label>
            <select
              className="form-select"
              onChange={(e) => handleOnChange(e)}
              name="marca"
              value={marca}
              required
            >
              <option value="">--SELECCIONE--</option>
              {marcas.map(({ _id, nombre }) => {
                return (
                  <option key={_id} value={_id}>
                    {nombre}
                  </option>
                );
              })}
            </select>
          </div>
        </div>
        <div className="col">
          <div className="mb-3">
            <label htmlFor="tipoEquipo" className="form-label">
              Tipo de Equipo
            </label>
            <select
              className="form-select"
              onChange={(e) => handleOnChange(e)}
              name="tipoEquipo"
              value={tipoEquipo}
              required
            >
              <option value="">--SELECCIONE--</option>
              {tipos.map(({ _id, nombre }) => {
                return (
                  <option key={_id} value={_id}>
                    {nombre}
                  </option>
                );
              })}
            </select>
          </div>
        </div>
        <div className="col">
          <div className="mb-3">
            <label htmlFor="estadoEquipo" className="form-label">
              Estado de Equipo
            </label>
            <select
              className="form-select"
              onChange={(e) => handleOnChange(e)}
              name="estadoEquipo"
              value={estadoEquipo}
              required
            >
              <option value="">--SELECCIONE--</option>
              {estados.map(({ _id, nombre }) => {
                return (
                  <option key={_id} value={_id}>
                    {nombre}
                  </option>
                );
              })}
            </select>
          </div>
        </div>
      </div>
      <div className="botones">
        <button className="btn btn-primary">Guardar</button>
        <button onClick={reload} className="btn btn-primary">
          Inventario
        </button>
      </div>
    </form>
  );
};
