import { useState, useEffect } from "react";
import BrandTable from "./TablaUsuario";
import { listarUsuarios } from '../../services/usuario.service'
import { UsuarioNuevo } from './CrearUsuario';

import Swal from "sweetalert2";

export const ListarUsuarios = () => {
  const [usuario, setUsuario] = useState([]);

      const mostrarUsuarios = async () => {
        try {
          const { data } = await listarUsuarios();
          setUsuario(data);
          Swal.fire({
            title:"Usuarios",
            allowOutsideClick: false,
            text: 'Cargando....',
            timer: 500
          });
          Swal.showLoading();
          
        } catch (error) {
          let mensaje;
          if (error && error.response && error.response.data){
            mensaje = error.response.data
          }else
          {
            mensaje = "Error de servidor, verifique el backend"
          }
          Swal.fire("Error", mensaje, "error");
        }
      };
    
    useEffect(() => {
        mostrarUsuarios();
    }, []);
    
    
      const DataTable = () => {

          return usuario.map((res, i) => {
        
              return <BrandTable obj={res} key={i}/>;
          });
          
      };

      return (
        
          <div>
             < UsuarioNuevo />
             <div className="fila-crear">
             <hr/>
              </div>          
              <div>
                  <table className="table table-sm border-primary table-striped">
                      <thead className="table-group-divider" >
                          <tr className="table-warning">
                              <th>#</th>
                              <th >Nombre</th>
                              <th >Email</th>
                              <th>Estado</th>
                              <th>Fecha de creacin</th>
                              <th>Fecha de Modificacin</th>
                              <th>Acciones</th>
                          </tr>
                      </thead>
                      <tbody className="table-group-divider">{DataTable()}</tbody>
                  </table>
              </div>
       
          </div>
      );
  };

