import { Axios } from "../helpers/axios.config";

export const listarEstados = () => {
    return Axios.get('estadoequipo');
}

export const crearEstados = (data) => {
    return Axios.post('estadoequipo', data);
}

export const editarEstados = (estadoequipoId, data) => {
    return Axios.put(`estadoequipo/${estadoequipoId}`, data);
}

export const eliminarEstado = (estadoequipoId) => {
    return Axios.delete(`estadoequipo/${estadoequipoId}`);
}

export const getEstado = (estadoequipoId) => {
    return Axios.get(`estadoequipo/${estadoequipoId}`);
}
