import { Axios } from "../helpers/axios.config";

export const listarMarcas = () => {
    return Axios.get('marca');
}

export const crearMarcas = (data) => {
    return Axios.post('marca', data);
}

export const editarMarcas = (marcaId, data) => {
    return Axios.put(`marca/${marcaId}`, data);
}

export const eliminarMarca = (marcaId) => {
    return Axios.delete(`marca/${marcaId}`);
}

export const getMarca = (marcaId) => {
    return Axios.get(`marca/${marcaId}`);
}