import { useState, useEffect } from "react";
import BrandTable from "./TablaTipo";
import { listarTipos } from '../../services/tipos.service'
import { TipoNuevo } from '../tipos/CrearTipo';

import Swal from "sweetalert2";

export const ListarTipos = () => {
  const [tipo, setTipo] = useState([]);

      const mostrarTipos = async () => {
        try {
          const { data } = await listarTipos();
          setTipo(data);
          Swal.fire({
            title:"Tipos de equipo",
            allowOutsideClick: false,
            text: 'Cargando....',
            timer: 500
          });
          Swal.showLoading();
          
        } catch (error) {
          let mensaje;
          if (error && error.response && error.response.data){
            mensaje = error.response.data
          }else
          {
            mensaje = "Error de servidor, verifique el backend"
          }
          Swal.fire("Error", mensaje, "error");
        }
      };
    
    useEffect(() => {
        mostrarTipos();
    }, []);
    
    
      const DataTable = () => {

          return tipo.map((res, i) => {
        
              return <BrandTable obj={res} key={i}/>;
          });
          
      };

      return (
        
          <div>
             < TipoNuevo />
             <div className="fila-crear">
             <hr/>
              </div>          
              <div>
                  <table className="table table-sm border-primary table-striped">
                      <thead className="table-group-divider" >
                          <tr className="table-warning">
                              <th>#</th>
                              <th >Nombre</th>
                              <th>Estado</th>
                              <th>Fecha de creacin</th>
                              <th>Fecha de Modificacin</th>
                              <th>Acciones</th>
                          </tr>
                      </thead>
                      <tbody className="table-group-divider">{DataTable()}</tbody>
                  </table>
              </div>
       
          </div>
      );
  };

