import React, { useState } from "react";
import { Link, Redirect } from "react-router-dom";
import { eliminarMarca } from "../../services/marca.service";
import Swal from "sweetalert2";
import { BsFillTrashFill, BsFillPenFill } from "react-icons/bs";

const BrandTable = (props) => {
  const { _id, nombre, estado, fechaCreacion, fechaActualizacion } = props.obj;

  const borrarMarca = async () => {
    try {
    Swal.fire({
      title: "Desea eliminar la marca",
      showDenyButton: true,
      showCancelButton: true,
      confirmButtonText: "Guardar",
      denyButtonText: "No guardar",
      cancelButtonText: "regresar"

    }).then(async (result) => {
      let data = "";
      try {
        if (result.isConfirmed) {
          data = await eliminarMarca(_id);
          Swal.fire({
            title: 'Marca eliminada correctamente!',
            text: 'Eliminada',        
          });
          setTimeout(() => {
            window.location.reload();
      }, 5000);
          
        } else if (result.isDenied) {
          Swal.fire("Los cambios no fueron guardados", "", "info");
        }
      } catch (error) {
        let mensaje;
        if (error && error.response && error.response.data) {
          mensaje = error.response.data;
        } else {
          mensaje = "Error de servidor, verifique el backend";
        }
        Swal.fire("Error", mensaje, "error");
      } finally {
        setTimeout(() => {
            console.log(data);
      }, 5000);
      }
    })
    } catch (error) {
        console.log(error);
    }
  };

  const [modalmarca, setModalMarca] = useState(false);

  const handleModalMarca = () => {
    setModalMarca(!modalmarca);
  };

  return (
    <>
      <tr>
        <th scope="row">{`${Array.prototype.length++}`}</th>
        <td>{nombre}</td>
        <td>{estado}</td>
        <td>{fechaCreacion}</td>
        <td>{fechaActualizacion}</td>

        <td className="botones">
          {modalmarca ? (
            <Redirect to ="/marca" />
            
          ) : (
            <Link className="edit-link" to={"/marca/edit/" + _id}>
              <button className="btn btn-secondary" onClick={handleModalMarca}>
                <BsFillPenFill />
              </button>
            </Link>
          )}
          <Link className="borrar-link" to={"/marca/borrar/" + _id}>
            <button className="btn btn-warning" onClick={borrarMarca}>
              <BsFillTrashFill />
            </button>
          </Link>
        </td>
      </tr>
    </>
  );
};

export default BrandTable;
